import React, { useState, useEffect } from 'react';
import { Form, Input, Upload, Button, Table, Modal, message, Typography } from 'antd';
import { PlusOutlined, DeleteOutlined, UploadOutlined, EditOutlined } from '@ant-design/icons';
import { Trash2 } from 'lucide-react';
import axios from 'axios';
import Cookies from 'js-cookie';
import CryptoJS from 'crypto-js';

const { Title } = Typography;
const API_BASE_URL = 'https://majesticmall.net/api/admin';
const { confirm } = Modal;

const AddBanner = () => {
  const [banners, setBanners] = useState([]);
  const [isAddModalVisible, setIsAddModalVisible] = useState(false);
  const [isEditModalVisible, setIsEditModalVisible] = useState(false);
  const [currentBanner, setCurrentBanner] = useState(null);
  const [form] = Form.useForm();
  const [editForm] = Form.useForm();

  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedToken = Cookies.get('token');
  const token = encryptedToken ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(CryptoJS.enc.Utf8) : null;

  useEffect(() => {
    fetchBanners();
  }, []);

  const fetchBanners = async () => {
    try {
      const response = await axios.get(`${API_BASE_URL}/getallbanner`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      setBanners(response.data.banner);
    } catch (error) {
      message.error('Failed to fetch banners');
    }
  };

  const handleAddBanner = async (values) => {
    try {
      const formData = new FormData();
      formData.append('banner', values.banner.file);
      formData.append('type', values.type);
      console.log("adinggggggggggggggg", values.banner.file);
      await axios.post(`${API_BASE_URL}/addbanner`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          Authorization: `Bearer ${token}`,
        },
      });

      message.success('Banner added successfully');
      setIsAddModalVisible(false);
      form.resetFields();
      fetchBanners();
    } catch (error) {
      message.error('Failed to add banner');
    }
  };

  const handleEditBanner = async (values) => {
    try {
      const formData = new FormData();
      if (values.banner && values.banner.file) {
        formData.append('banner', values.banner.file);
        // formData.append('banner', values.banner[0].originFileObj);
      }
      formData.append('type', values.type);

      console.log("bannerrrrrrrrrrrrr", values.banner.file);

      await axios.put(`${API_BASE_URL}/updatebanner/${currentBanner._id}`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          Authorization: `Bearer ${token}`,
        },
      });

      message.success('Banner updated successfully');
      setIsEditModalVisible(false);
      editForm.resetFields();
      fetchBanners();
    } catch (error) {
      message.error('Failed to update banner');
    }
  };

  const showEditModal = (banner) => {
    setCurrentBanner(banner);
    editForm.setFieldsValue({
      type: banner.type,
    });
    setIsEditModalVisible(true);
  };

  const handleDeleteBanner = async (id) => {
    try {
      await axios.post(`${API_BASE_URL}/deletebanner/${id}`, {}, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      message.success('Banner deleted successfully');
      fetchBanners();
    } catch (error) {
      message.error('Failed to delete banner');
    }
  };

  const showDeleteConfirm = (id) => {
    confirm({
      title: "Are you sure you want to delete?",
      content: "This action cannot be undone.",
      okText: "Yes",
      okType: "danger",
      cancelText: "No",
      onOk() {
        handleDeleteBanner(id);
      },
      onCancel() {
        // console.log("Deletion cancelled");
      },
    });
  };

  const columns = [
    { title: 'Serial No.', dataIndex: 'serialNo', key: 'serialNo' },
    { title: 'Title', dataIndex: 'type', key: 'type' },
    {
      title: 'Banner',
      dataIndex: 'banner',
      key: 'banner',
      render: (banner) => (
        <img
          src={`https://majesticmall.net/image/${banner}`}
          alt="Banner"
          style={{ width: 100 }}
          loading="lazy"
          onError={(e) => {
            e.target.src = '../img/example.jpg';
          }}
        />
      ),
    },
    {
      title: 'Action',
      key: 'action',
      render: (_, record) => (
        <div style={{ display: 'flex', gap: '8px' }}>
          <Button
            type="text"
            icon={<EditOutlined />}
            onClick={() => showEditModal(record)}
          />
          <Button
            type="text"
            icon={<Trash2 color="red" />}
            onClick={() => showDeleteConfirm(record._id)}
          />
        </div>
      ),
    },
  ];

  const dataSource = banners.map((banner, index) => ({
    key: banner._id,
    serialNo: index + 1,
    type: banner.type,
    banner: banner.banner,
    _id: banner._id,
  }));

  return (
    <div>
      <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: 16 }}>
        <Title level={3} className="title">Banners</Title>
        <Button type="primary" icon={<PlusOutlined />} onClick={() => setIsAddModalVisible(true)}>
          Add Banner
        </Button>
      </div>

      <Table
        columns={columns}
        dataSource={dataSource}
        pagination={{
          pageSize: 10,
        }}
        scroll={{ x: "max-content" }}
        components={{
          header: {
            cell: (props) => (
              <th
                {...props}
                style={{
                  ...props.style,
                  background: "linear-gradient(#5C870C,#5C870C,#5C870C)",
                  color: "white",
                }}
              />
            ),
          },
        }}
      />

      {/* Add Banner Modal */}
      <Modal
        title="Add Banner"
        visible={isAddModalVisible}
        onCancel={() => setIsAddModalVisible(false)}
        footer={null}
      >
        <Form form={form} onFinish={handleAddBanner} layout="vertical">
          <Form.Item
            name="banner"
            label="Banner Image (Size: 1392*522 Pixels)"
            rules={[{ required: true, message: 'Please upload a banner image' }]}
          >
            <Upload accept="image/*" listType="picture-card" maxCount={1} beforeUpload={() => false}>
              <div>
                <PlusOutlined />
                <div style={{ marginTop: 8 }}>Upload</div>
              </div>
            </Upload>
          </Form.Item>
          <Form.Item
            name="type"
            label="Title"
            rules={[{ required: true, message: 'Please enter a title' }]}
          >
            <Input />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              Add Banner
            </Button>
          </Form.Item>
        </Form>
      </Modal>

      {/* Edit Banner Modal */}
      <Modal
        title="Edit Banner"
        visible={isEditModalVisible}
        onCancel={() => setIsEditModalVisible(false)}
        footer={null}
      >
        <Form form={editForm} onFinish={handleEditBanner} layout="vertical">
          <Form.Item
            name="banner"
            label="Banner Image (Size: 1392*522 Pixels)"
          // valuePropName="fileList"
          // getValueFromEvent={(e) => {
          //   if (Array.isArray(e)) {
          //     return e;
          //   }
          //   return e && e.fileList;
          // }}
          >
            <Upload
              accept="image/*"
              listType="picture-card"
              maxCount={1}
              beforeUpload={() => false}
              defaultFileList={
                currentBanner
                  ? [{
                    uid: '-1',
                    name: 'current-banner',
                    status: 'done',
                    url: `https://majesticmall.net/image/${currentBanner.banner}`,
                  }]
                  : []
              }
            >
              <div>
                <PlusOutlined />
                <div style={{ marginTop: 8 }}>Upload</div>
              </div>
            </Upload>
          </Form.Item>
          <Form.Item
            name="type"
            label="Title"
            rules={[{ required: true, message: 'Please enter a title' }]}
          >
            <Input />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              Update Banner
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
};

export default AddBanner;